	header .custom-nav {
		margin: 1.5rem;
	}
}
header .custom-nav .nav-button {
	padding: 0;
	border: none;
	background: none;
	display: flex;
	align-items: center;
	height: 100%;
}
@media (min-width: 64em) {
	header .custom-nav .nav-button:focus {
		outline: 0.5px dashed white;
	}
}
header .custom-nav .nav-button .flex-box .nav-close,
header .custom-nav .nav-button .flex-box .nav-open {
	display: block;
	width: 1.5rem;
	fill: white;
	cursor: pointer;
}
